<template>
  <!-- Modal -->
  <transition name="fadeIn">
    <Modal @closeModal="open_modal = false" :onerooms="onerooms" :prod_num="prod_num" :open_modal="open_modal" />
  </transition>
  <!-- // Modal -->

  <!-- Menu -->
  <div class="menu mb-5">
    <a v-for="nav_a in nav" :key="nav_a">{{ nav_a }}</a>
  </div>
  <!-- // Menu -->

  <!-- Discount -->
  <Discount />
  <!-- // Discount -->

  <!-- PriceSort -->
  <button v-if="show_price_sort_btn == true" @click="[priceSortUp(), show_price_sort_btn = false]" class="btn btn-info mr-3">Í∞ÄÍ≤©Ïàú Ï†ïÎ†¨(Ïò§Î¶ÑÏ∞®Ïàú)</button>
  <button v-else @click="[priceSortDown(), show_price_sort_btn = true]" class="btn btn-info mr-3">Í∞ÄÍ≤©Ïàú Ï†ïÎ†¨(ÎÇ¥Î¶ºÏ∞®Ïàú)</button>
  <button v-if="show_name_sort_btn == true" @click="[nameSortUp(), show_name_sort_btn = false]" class="btn btn-info mr-3">Ïù¥Î¶ÑÏàú Ï†ïÎ†¨(Ïò§Î¶ÑÏ∞®Ïàú)</button>
  <button v-else @click="[nameSortDown(), show_name_sort_btn = true]" class="btn btn-info mr-3">Ïù¥Î¶ÑÏàú Ï†ïÎ†¨(ÎÇ¥Î¶ºÏ∞®Ïàú)</button>
  <button @click="halfmillionSortDown()" class="btn btn-info mr-3">50ÎßåÏõê ÎØ∏Îßå</button>
  <button @click="sortBack" class="btn btn-info">ÎêòÎèåÎ¶¨Í∏∞</button>
  <!-- // PriceSort -->

  <!-- Products -->
  <Card @openModal="open_modal = true; prod_num = $event" @oneroomCount="onerooms[i].count++" :oneroom="onerooms[i]"
    :prod_num="prod_num" v-for="(card_box, i) in onerooms" :key="i" />
  <!-- // Products -->
</template>

<script>
  import oneroom_cont from './oneroom.js'
  import Discount from './Discount.vue'
  import Modal from './components/Modal.vue'
  import Card from './components/Card.vue'

  export default {
    name: 'App',
    data() {
      return {
        nav: ['Home', 'Products', 'About'],
        onerooms_origin: [...oneroom_cont],
        onerooms: oneroom_cont,
        prod_num: 0,
        open_modal: false,
        show_price_sort_btn: true,
        show_name_sort_btn: true,
      }
    },

    methods: {
      priceSortUp() {
        this.onerooms.sort(function(a, b){
          return a.price - b.price
        })
      },
      priceSortDown() {
        this.onerooms.sort(function(a, b){
          return b.price - a.price
        })
      },
      nameSortUp() {
        this.onerooms.sort(function(a, b) {
          return a.title < b.title ? -1 : a.title > b.title ? 1 : 0;
        })
      },
      nameSortDown() {
        this.onerooms.sort(function(a, b) {
          return a.title > b.title ? -1 : a.title < b.title ? 1 : 0;
        })
      },
      halfmillionSortDown() {
        this.onerooms = this.onerooms.filter(function(data) {
          return data.price < '50';
        })
      },
      sortBack() {
        this.onerooms = [...this.onerooms_origin];
      }
    },

    components: {
      Discount: Discount,
      Modal: Modal,
      Card: Card,
    }
  }
</script>

